body {
  padding: 50px 0;
  min-width: 880px; }

h1 {
  color: #fff;
  font-size: 2.8rem;
  font-weight: 500;
  text-align: center;
  margin-bottom: 45px; }

h2, h3 {
  text-transform: uppercase;
  font-size: 1rem;
  line-height: 1.3rem; }

h2 {
  margin: 1rem 0 0 0;
  font-weight: 700;
  color: #00adef; }

h3 {
  color: #999;
  font-weight: 500; }

h3, .degrees, .precipitation {
  color: #999; }

.days, .details {
  background-color: #f3f2f1;
  width: 75%;
  max-width: 1060px;
  margin: 0 auto;
  border: 1px solid #999;
  font-size: 0;
  margin-bottom: 30px; }

.days {
  border-top: 35px solid #444;
  margin-bottom: 6px; }

.day, .daytime, .nighttime {
  font-size: 1rem;
  display: inline-block;
  padding: 0 1rem;
  border-right: 1px solid #999; }
  .day p, .daytime p, .nighttime p {
    line-height: 1.3rem; }

.day {
  width: 20%;
  height: 290px;
  overflow: hidden;
  cursor: pointer; }
  .day:last-child {
    border-right: none; }
  .day img {
    display: block;
    width: 70%; }
  .day .degrees {
    font-size: 1.3rem; }
    .day .degrees .high {
      color: #444;
      font-size: 2rem; }
  .day.selected {
    background-color: #fff;
    border-bottom: 5px solid #f7931e; }

.details {
  border-top: 5px solid #444;
  background-color: #fff; }
  .details .daytime, .details .nighttime {
    width: 50%; }
  .details .day-night-info {
    font-size: 0; }
  .details .left-side, .details img {
    width: 50%;
    vertical-align: top; }
  .details .left-side {
    display: inline-block;
    font-size: 1rem; }
    .details .left-side .temp {
      font-size: 2.5rem;
      margin-top: 10px;
      position: relative; }
      .details .left-side .temp .label {
        text-transform: uppercase;
        color: #999;
        font-size: .7rem;
        position: absolute;
        top: 10px;
        left: 55px; }
    .details .left-side .precipitation {
      margin-top: 64px; }



